<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process162"></a>

<p><br></p>
<p><span class="rvts17">暴力接管（</span><span class="rvts50">Aggressive Takeover</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:319,Y:148</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：加文（</span><span class="rvts20">Garvan</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">报告加文莱姆被害一事（需要首先完成上篇的</span><span class="rvts20"></span><span class="rvts21">账本上的红墨迹</span><span class="rvts20"></span><span class="rvts21">支线），并为莱姆复仇</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1133.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">回城之后，尝试配出</span><span class="rvts20"></span><span class="rvts21">有毒的矮人肉汤</span><span class="rvts20"></span><span class="rvts21">为莱姆复仇，其中矮人肉汤在酒馆老板娘处有卖，配合着有毒的鱼（必须是从商人处买到的毒鱼）或者有毒的物品，给加文合成两份不同的</span><span class="rvts20"></span><span class="rvts21">有毒的矮人肉汤</span><span class="rvts20"></span><span class="rvts21">，并让他品尝。</span>
<br><span class="rvts21">他会被毒死，并且不会引起酒馆以及整个城镇的恐慌。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1134.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">如果上述方法无效，可以尝试使用毒雾技能或其它的一些不引起城镇市民注意的方式，杀掉他（最好使用一名不死队友，放毒雾，如果引发战斗，就装死；或者把他传送出来，找个人少的地方杀掉）。之后便可以获得他的人头。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1135.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">把加文的人头交给莱姆，莱姆会为此事平息，并告诉他未找到的宝藏位置（</span><span class="rvts20">X:142 Y:229</span><span class="rvts21">），该任务关闭，</span><span class="rvts20"></span><span class="rvts21">账本上的红墨迹支线</span><span class="rvts20"></span><span class="rvts21">也会同时关闭。</span>
<br><span class="rvts21">该宝藏其实即便不在他这里获得位置信息，只要有足够的智慧值（</span><span class="rvts20">Wit</span><span class="rvts21">），也会自己发现。</span>
<br><span class="rvts21">另外，吃掉</span><span class="rvts20"></span><span class="rvts21">加文</span><span class="rvts20"></span><span class="rvts21">的头，会获得</span><span class="rvts20"></span><span class="rvts21">商人的秘密</span><span class="rvts20"></span><span class="rvts21">天赋，该天赋</span><span class="rvts20">+1</span><span class="rvts21">讨价还价。但是如果吃掉加文的头，莱姆就不会告诉你宝藏的位置。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



